@mixin rotationFix($order, $degrees) {
    &:nth-child(#{$order + 1}) {
        transform: rotate(#{$order * $degrees}deg);

        .counter-rotation-element {
            transform: rotate(-#{$order * $degrees}deg);
        }
    }
}

@mixin rotationFixChilds($elements) {
    $i: 0;
    $degrees: calc(360/$elements);

    @while $i < $elements {
        @include rotationFix($i, $degrees);
        $i: $i + 1;
    }
}

@keyframes rotationLoop {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes counterRotationLoop {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(-360deg);
    }
}

.rotation {
    position: relative;

    .rotation-container {
        width: 100%;
        height: 100%;
    }

    .rotation-circle {
        width: 100%;
        height: 100%;
        border-radius: 1000px;
        border: $strokeWidth solid $borderColor;
        position: relative;
    }

    .rotation-element {
        height: 50%;
        position: absolute;
        text-align: center;
        transform-origin: bottom center;
    }

    &.turn {
        .rotation-container {
            animation: rotationLoop 30s linear infinite;
        }

        .rotation-element .counter-rotation-element>* {
            animation: counterRotationLoop 30s linear infinite;
        }
    }

    &.elements-2 .rotation-element {
        @include rotationFixChilds(2);
    }

    &.elements-3 .rotation-element {
        @include rotationFixChilds(3);
    }

    &.elements-4 .rotation-element {
        @include rotationFixChilds(4);
    }

    &.elements-5 .rotation-element {
        @include rotationFixChilds(5);
    }

    &.elements-6 .rotation-element {
        @include rotationFixChilds(6);
    }

    &.elements-7 .rotation-element {
        @include rotationFixChilds(7);
    }

    &.elements-8 .rotation-element {
        @include rotationFixChilds(8);
    }

    @media (max-width: $smallView) {
        &.turn {
            .rotation-container {
                animation: none !important;
            }

            .rotation-element .counter-rotation-element>* {
                animation: none !important;
            }
        }
    }
}