@mixin timelineItemDelay ($order) {
    &:nth-child(#{$order}) {
        animation: scaleUp $transitionTimer ease-in-out forwards;
        animation-delay: calc(($transitionTimer + 0.2s) * $order);
    }
}

.experiences {
    top: 0;
    width: 100%;
    display: flex;
    position: absolute;
    justify-content: center;

    .center-line-container {
        position: fixed;
    }

    .timeline {
        height: 100%;
        margin-top: 200px;

        .timeline-item {
            transform: scale(0);
            position: relative;
            @include timelineItemDelay(1);
            @include timelineItemDelay(2);
            @include timelineItemDelay(3);
            @include timelineItemDelay(4);
            @include timelineItemDelay(5);
            @include timelineItemDelay(6);
            @include timelineItemDelay(7);

            .timeline-button {
                position: relative;
                transition: $transitionTimer;

                .content {
                    background-color: white !important;

                    div {
                        width: 100%;
                        height: 100%;
                        background-size: 80px;
                        background-position: center;
                        background-repeat: no-repeat;

                        &.erasmus {
                            background-image: url('../../static/img/erasmuslogo.jpg');
                        }

                        &.sciensano {
                            background-image: url('../../static/img/sciensanologo.jpeg');
                        }

                        &.efficy {
                            background-image: url('../../static/img/efficy.png');
                        }

                        &.capgemini {
                            background-image: url('../../static/img/capgemini.jpg');
                        }

                        &.monizze {
                            background-image: url('../../static/img/monizze.png');
                        }
                    }
                }

                p {
                    top: 40px;
                    width: 100px;
                    font-size: 0.8em;
                    position: absolute;
                }
            }

            &:nth-child(even) {
                .timeline-button p {
                    left: 120px;
                    text-align: left;
                }

                .timeline-description {
                    right: 150px;

                    .octogon .triangle {
                        transform: rotate(90deg);
                        right: -26px;
                    }
                }
            }

            &:nth-child(odd) {
                .timeline-button p {
                    right: 120px;
                    text-align: right;
                }

                .timeline-description {
                    left: 150px;

                    .octogon .triangle {
                        transform: rotate(-90deg);
                        left: -26px;
                    }
                }
            }

            .timeline-description {
                top: 10px;
                z-index: 1;
                position: absolute;
                transition: $transitionTimer;

                ul svg {
                    margin-right: 5px;
                    display: inline-block;

                    polygon {
                        fill: $theme;
                    }
                }

                &.short {
                    width: 400px;
                }

                &.long {
                    width: calc(50vw - 130px);
                }

                .octogon {
                    position: relative;
                    transition: $transitionTimer;

                    .timeline-description-content {
                        margin-bottom: 10px;
                    }

                    .tiny {
                        margin-bottom: 10px;
                    }

                    .triangle {
                        position: absolute;
                        top: 25px;
                    }

                    .hexagon,
                    .circle {
                        margin: 0 5px;
                        font-size: 0.9em;
                        display: inline-block;
                    }
                }

                .timeline-description-buttons {
                    text-align: center;
                    margin-bottom: 10px;
                }
            }
        }
    }

    @media (max-width: $largeView) {
        display: block;
        padding-left: 21px;

        .timeline-button p {
            top: 120px !important;
            left: 0 !important;
            text-align: center !important;
        }

        .timeline-description {
            left: 150px !important;
            width: calc(100% - 195px) !important;

            .octogon .triangle {
                transform: rotate(-90deg) !important;
                left: -26px !important;
            }
        }
    }

    @media (max-width: $smallView) {
        margin-left: 0 !important;
        padding: 0 20px;

        .timeline-button {
            top: -60px;
            right: 20px;
            z-index: 2;
            transform: scale(0.7);
            position: absolute !important;

            p {
                display: none;
            }
        }

        .timeline-description {
            font-size: 0.9em;
            width: 100% !important;
            top: initial !important;
            left: initial !important;
            right: initial !important;
            position: relative !important;
            margin-bottom: 60px !important;

            .octogon {
                width: 100% !important;
            }

            .octogon .triangle {
                display: none;
            }
        }

        .center-line-container {
            display: none;
        }
    }
}