$darkBorderColor: #666;
$darkBackgroundColor: #111;
$darkBodyBackground: #101010fa;

.dark {
    min-height: 100vh;
    background-repeat: repeat;
    background: linear-gradient($darkBodyBackground, $darkBodyBackground),
        url('../../static/img/background.jpg');
    background-size: 300px 300px;
    background-color: $darkBackgroundColor;
    color: $white;

    .experiences {
        min-height: 100vh;
        background-repeat: repeat;
        background: linear-gradient($darkBodyBackground, $darkBodyBackground),
            url('../../static/img/background.jpg');
        background-size: 300px 300px;
        background-color: $darkBackgroundColor;
    }

    .bar,
    .shape-container {
        .border {
            background-color: $darkBorderColor;

            &::before {
                border-bottom-color: $darkBorderColor !important;
            }

            &::after {
                border-top-color: $darkBorderColor !important;
            }
        }

        .content {
            background-color: $darkBackgroundColor;

            &::before {
                border-bottom-color: $darkBackgroundColor !important;
            }

            &::after {
                border-top-color: $darkBackgroundColor !important;
            }
        }
    }

    .shape-container {
        &.active {
            .border {
                background-color: $white;

                .content {
                    background-color: $darkBorderColor;
                }
            }
        }


        &.cursor-pointer:hover .content {
            background-color: lighten($darkBackgroundColor, 10%) !important;
        }
    }

    nav .ornament {
        div {
            background-color: $darkBorderColor;
        }

        svg {
            stroke: $darkBorderColor;
        }
    }

    .center-line {
        background-color: $darkBorderColor;

        &::after,
        &::before {
            border-color: $darkBorderColor;
        }
    }

    .bar .border {
        p {
            color: $white
        }

        .liquid {
            svg {
                animation: fadeInDark 0.5s ease-in-out forwards;
                animation-delay: 1.2s;
            }

            &.bg-blue {
                background-color: darken($blue, 20%);
            }

            &.bg-red {
                background-color: darken($red, 20%);
            }

            &.bg-green {
                background-color: darken($green, 20%);
            }

            &.bg-purple {
                background-color: darken($purple, 20%);
            }

            &.bg-yellow {
                background-color: darken($yellow, 20%);
            }

            &.bg-orange {
                background-color: darken($orange, 30%);
            }
        }
    }

    .center-chart {
        .slice {
            color: $white;

            &:nth-child(1) .background {
                background: linear-gradient(darken($red, 30%), darken($red, 20%));
            }

            &:nth-child(2) .background {
                background: linear-gradient(darken($blue, 30%), darken($blue, 20%));
            }

            &:nth-child(3) .background {
                background: linear-gradient(darken($green, 30%), darken($green, 20%));
            }
        }
    }

    .technologies .hexagon .content {
        color: $white;

        div {
            filter: brightness(0.5) invert(0.7) grayscale(1);
        }
    }

    .falling-border .border {
        background: linear-gradient($borderColor 52%, $backgroundColor 53%);
        background-size: 220% 220%;
        background-position: 100% 100%;
        animation: borderDown 0.6s linear forwards;
        animation-delay: 0.4s;
    }
}

@keyframes fadeInDark {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}