nav {
    width: 100%;
    z-index: 100;
    position: absolute;

    & .ornament {

        & .left-line,
        & .right-line {
            height: $strokeWidth;
            background-color: $borderColor;
            display: inline-block;
            width: calc(50% - 56px);
            vertical-align: top;
            margin-top: 37px;
        }

        & .left-line {
            margin-left: 20px;
        }

        & .right-line {
            margin-right: 20px;
        }

        & svg {
            stroke-width: $strokeWidth;
            stroke: $borderColor;
            display: inline-block;
        }
    }

    & .toggler {
        top: 11px;
        z-index: 3;
        width: 56px;
        height: 56px;
        position: absolute;
        left: calc(50% - 28px);

        & .border {
            width: 56px;
            height: 56px;
        }
    }

    & .links {
        width: 240px;
        height: 100px;
        margin-top: -30px;
        position: relative;
        margin-left: calc(50% - 120px);

        & .link {
            opacity: 0;
            top: -30px;
            position: absolute;
            left: calc(50% - 23px);
            transition: $transitionTimer;

            &:nth-child(1) {
                transition-delay: 0.1s;
            }

            &:nth-child(2) {
                transition-delay: 0.2s;
            }

            &:nth-child(3) {
                transition-delay: 0.3s;
            }

            &:nth-child(4) {
                transition-delay: 0.4s;
            }

            & .border {
                width: 46px;
                height: 46px;
            }
        }
    }


    &.active {
        & .links {
            display: block !important;

            & .link {
                opacity: 1;

                &:nth-child(1) {
                    top: 0;
                    left: 25px;
                }

                &:nth-child(2) {
                    top: 40px;
                    left: 65px;
                }

                &:nth-child(3) {
                    top: 40px;
                    left: calc(100% - 110px);
                }

                &:nth-child(4) {
                    top: 0;
                    left: calc(100% - 70px);
                }
            }
        }
    }
}