.secret {
    .center-triangle {
        width: 100%;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;

        .triangle .border {
            overflow: hidden;
        }

        .rotation {
            z-index: 1;
            margin-top: 40px;
            position: absolute;
            transform: rotate(60deg);

            .rotation-element {
                top: -50%;
                scale: 0.5;
                opacity: 0;
                height: 100%;
                left: calc(50% - 35px);
                animation: scaleUpSecret $transitionTimer ease-in-out forwards;
                animation-delay: 1s;

                .shape-container {
                    transform: rotate(-60deg);

                    .content {
                        font-size: 1.5em;
                    }

                    &.square.unlocked .border {
                        background: linear-gradient($purple, darken($purple, 20%));
                    }

                    &.circle.unlocked .border {
                        background: linear-gradient($yellow, darken($yellow, 20%));
                    }

                    &.hexagon.unlocked .border {
                        background: linear-gradient($red, darken($red, 20%));
                    }
                }
            }
        }
    }

    @media (max-width: $smallView) {
        .shape-container.triangle {
            width: 200px !important;
            height: 200px !important;
        }

        .rotation {
            width: 100px !important;
            height: 100px !important;

            .rotation-element {
                left: calc(50% - 19px);
            }

            .shape-container .border {
                transform: scale(0.7);
            }
        }
    }
}

@keyframes shoveSecretShapes {
    0% {
        transform: translateY(400px);
    }

    100% {
        transform: translateY(30px);
    }
}

@keyframes scaleUpSecret {
    0% {
        scale: 0.5;
        opacity: 0;
    }

    100% {
        scale: 1;
        opacity: 1;
    }
}