.music-player {
    gap: 10px;
    width: 400px;
    bottom: 20px;
    display: flex;
    z-index: 100;
    position: fixed;
    align-items: center;
    justify-content: center;
    left: calc(50% - 200px);

    .octogon {
        flex-grow: 1;
        text-align: center;
        position: relative;
        font-size: 0.8em;
    }

    @media (max-width: $smallView) {
        left: 20px;
        width: calc(100% - 40px);
    }
}