.center-line-container {
    display: flex;
    padding-top: 77px;
    position: absolute;
    justify-content: center;
    width: 100%;

    &.md {
        height: calc(50vh - 78px);
    }

    &.lg {
        height: 100vh;
    }

    .center-line {
        width: $strokeWidth;
        height: 0px;
        animation-iteration-count: 1;
        animation: lineDown 0.6s ease-in-out forwards;
        position: relative;

        &::after {
            position: absolute;
            border-top: 1px solid;
            border-left: 1px solid;
            border-top-left-radius: 5px;
            width: calc(50vw - 80px);
            height: 10px;
            top: -10px;
        }

        &::before {
            position: absolute;
            border-bottom: 1px solid $borderColor;
            border-right: 1px solid $borderColor;
            border-bottom-right-radius: 5px;
            width: 10.5px;
            height: 69px;
            top: -78px;
            left: calc(50vw - 80px);
        }
    }



    @media (max-width: $largeView) {
        &.lg {
            display: block;
            padding-top: 170px;
            left: 70px;

            .center-line::before,
            .center-line::after {
                content: "";
            }
        }
    }
}

@keyframes lineDown {
    0% {
        height: 0;
    }

    100% {
        height: 100%;
    }
}