.info-container .description {
    top: 0;
    gap: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    position: absolute;
    align-items: center;
    transform: scale(0);
    justify-content: center;
    animation: descriptionSlideOut $transitionTimer linear forwards;

    .shape-container.octogon {
        width: 50%;

        &:first-child {
            margin-left: 20px;

            .triangle {
                top: calc(50% - 15px);
                right: -26px;
                position: absolute;
                transform: rotate(90deg);

                .content {
                    padding: 10px 0 0 0;
                }
            }
        }

        &:nth-child(2) {
            margin-right: 20px;

            .triangle {
                top: calc(50% - 15px);
                left: -26px;
                position: absolute;
                transform: rotate(-90deg);

                .content {
                    padding: 10px 0 0 0;
                }
            }
        }
    }

    @media (max-width: $largeView) {
        height: auto;
        display: block;
        padding: 0 20px 20px 20px;
        position: relative;
        margin-top: calc(-50vh + 200px);

        .shape-container.octogon {
            width: 100%;
            margin: 0 0 30px 0 !important;

            .triangle {
                display: none;
            }
        }
    }

    @media (max-width: $mediumView) {
        margin-top: calc(-50vh + 210px);
    }

    @media (max-width: $smallView) {
        margin-top: calc(-50vh + 140px);

        .shape-container.octogon {
            font-size: 0.9em;
        }
    }
}

@keyframes descriptionSlideOut {
    0% {
        gap: 0;
        transform: scale(0);
    }

    100% {
        gap: 500px;
        transform: scale(1);
    }
}