.info-container .hobbies {
    top: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;

    .rotation {
        animation: scaleUp $transitionTimer ease-in-out forwards;

        .rotation-element {
            top: -30px;
            left: calc(50% - 30px);
            width: auto;
            height: calc(50% + 30px);
            z-index: 10;
        }
    }

    .hexagon .content {
        font-size: 1.4em;
    }

    .octogon .content {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 20px;

        p {
            max-width: 400px;
        }
    }

    .selected-hobby {
        bottom: 80px;
        position: absolute;
        margin: 0 30px;

        .octogon .content {
            padding: 15px 20px;

            .hexagon {
                min-width: 58px;
            }
        }
    }

    @media (max-width: $smallView) {
        .rotation {
            width: 300px !important;
            height: 300px !important;

            .shape-container.hexagon .border {
                transform: scale(0.9);
            }
        }

        .selected-hobby {
            font-size: 0.9em;
        }
    }
}