h1 {
    text-align: center;
    margin: 5px 0 10px 0;
    font-family: $titleFont;

    &.sm {
        font-size: 1.2em;
    }

    &.md {
        font-size: 2em;
    }

    &.lg {
        font-size: 2.5em;
    }

    &.with-lines {

        &:after,
        &:before {
            top: 1.2em;
            content: "";
            position: absolute;
            height: $strokeWidth;
            width: calc(50% - 160px);
            background-color: $borderColor;
        }

        &:after {
            left: calc(50% + 140px);
        }

        &:before {
            transform-origin: 0%;
            left: calc(50% - 140px);
            transform: rotate(180deg);
        }
    }
}