.info-container .about-info {
    top: 0;
    gap: 200px;
    width: 100%;
    height: 100vh;
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;

    .about-info-left,
    .about-info-right {
        padding: 20px;
        overflow: hidden;
    }

    .octogon {
        position: relative;

        p {
            width: 100%;
            text-align: center;
            vertical-align: top;
            display: inline-block;

            &:first-child {
                color: $theme;
            }

            &:not(:first-child) {
                font-size: 0.9em;
            }
        }

        .hexagon {
            position: absolute;
            top: calc(50% - 20px);
            left: 15px;
        }
    }

    .about-info-left .octogon {
        width: 250px;
        transform-origin: 500px center;

        &:not(:nth-child(2)) {
            margin-left: 90px;
        }

        &:first-child {
            transform: rotate(-90deg);
            animation: info1 0.6s ease-in-out forwards;
        }

        &:nth-child(2) {
            transform: translateX(500px);
            animation: leftInfo2 0.6s ease-in-out forwards;
        }

        &:last-child {
            transform: rotate(90deg);
            animation: info3 0.6s ease-in-out forwards;
        }

        &:not(:last-child) {
            margin-bottom: 100px;
        }
    }

    .about-info-right .octogon {
        width: 250px;
        transform-origin: -250px center;

        &:nth-child(2) {
            margin-left: 90px;
        }

        &:first-child {
            transform: rotate(90deg);
            animation: info3 0.6s ease-in-out forwards;
        }

        &:nth-child(2) {
            transform: translateX(-500px);
            animation: rightInfo2 0.6s ease-in-out forwards;
        }

        &:last-child {
            transform: rotate(-90deg);
            animation: info1 0.6s ease-in-out forwards;
        }

        &:not(:last-child) {
            margin-bottom: 100px;
        }
    }

    @media (max-width: $largeView) {
        gap: 300px;

        .shape-container.octogon {
            width: 100%;
            margin: 0 0 30px 0 !important;

            .triangle {
                display: none;
            }
        }

        .about-info-left,
        .about-info-right {
            width: 100%;
        }

        .about-info-left .octogon {
            transform-origin: 200% center;
        }

        .about-info-right .octogon {
            transform-origin: -100% center;
        }
    }

    @media (max-width: $mediumView) {
        height: auto;
        display: block;
        padding: 0 20px;
        position: relative;
        transform: scale(1);
        animation: none;
        margin-top: calc(-50vh + 210px);

        .about-info-left,
        .about-info-right {
            width: 50% !important;
            display: inline-block;
            overflow: visible;
            padding: 0;

            .octogon {
                transform-origin: center;
                animation: scaleUp $transitionTimer ease-in-out forwards !important;
            }
        }

        .about-info-left {
            padding-right: 10px;
        }

        .about-info-right {
            padding-left: 10px;
        }
    }

    @media (max-width: $smallView) {
        margin-top: calc(-50vh + 140px);

        .about-info-left,
        .about-info-right {
            width: 100% !important;
            padding: 0;

            .octogon {
                animation: scaleUp $transitionTimer ease-in-out forwards !important;
            }
        }
    }
}

@keyframes info1 {
    0% {
        transform: rotate(-90deg);
    }

    100% {
        transform: rotate(0);
    }
}

@keyframes leftInfo2 {
    0% {
        transform: translateX(500px);
    }

    100% {
        transform: translateX(0);
    }
}

@keyframes rightInfo2 {
    0% {
        transform: translateX(-500px);
    }

    100% {
        transform: translateX(0);
    }
}

@keyframes info3 {
    0% {
        transform: rotate(90deg);
    }

    100% {
        transform: rotate(0);
    }
}