.contact {
    top: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;

    .rotation {
        transform: rotate(30deg);
        transform-origin: center center;

        .rotation-element {
            top: -50px;
            left: calc(50% - 50px);
            width: auto;
            height: calc(50% + 50px);

            .shape-container {
                transform: translateY(400px);
                animation: shoveContactTriangle $transitionTimer ease-in-out forwards;
            }

            &:nth-child(1) .shape-container {
                animation-delay: 0.1s;

                svg {
                    transform: rotate(-30deg);
                }
            }

            &:nth-child(2) .shape-container {
                animation-delay: 0.2s;

                svg {
                    transform: rotate(-90deg);
                }
            }

            &:nth-child(3) .shape-container {
                animation-delay: 0.3s;

                svg {
                    transform: rotate(-150deg);
                }
            }

            &:nth-child(4) .shape-container {
                animation-delay: 0.4s;

                svg {
                    transform: rotate(-210deg);
                }
            }

            &:nth-child(5) .shape-container {
                animation-delay: 0.5s;

                svg {
                    transform: rotate(-270deg);
                }
            }

            &:nth-child(6) .shape-container {
                animation-delay: 0.6s;

                svg {
                    transform: rotate(-330deg);
                }
            }
        }

        .counter-rotation-element {
            transform: none !important;
        }
    }

    @media (max-width: $smallView) {
        .rotation {
            width: 300px !important;
            height: 300px !important;

            .shape-container {
                transform: translateY(300px);
                animation: shoveContactTriangleTiny $transitionTimer ease-in-out forwards !important;
            }
        }
    }
}

@keyframes shoveContactTriangle {
    0% {
        transform: translateY(400px);
    }

    100% {
        transform: translateY(30px);
    }
}

@keyframes shoveContactTriangleTiny {
    0% {
        transform: translateY(300px);
    }

    100% {
        transform: translateY(30px);
    }
}