.name {
    top: 40px;
    left: 30px;
    z-index: 101;
    text-align: left;
    position: absolute;

    p:first-child,
    p:nth-child(3) {
        font-size: 1.5em;
        font-weight: bold;
        font-family: $nameFont;
    }

    p:nth-child(2),
    p:nth-child(4) {
        opacity: 0.7;
        font-size: 0.8em;
    }

    .fancy-text {
        font-size: 1.5em;
    }

    .desktop {
        display: block;
    }

    .mobile {
        display: none;
    }

    @media (max-width: $smallView) {
        left: 20px;

        .desktop {
            display: none;
        }

        .mobile {
            display: block;
        }
    }
}