body {
    font-family: $bodyFont;
}

hr {
    margin: 10px 0;
    border-color: $borderColor;
}

*::-webkit-scrollbar {
    display: none;
    width: 0;
    opacity: 0;
}

.no-transition * {
    transition: none !important;
    transition-delay: initial !important;
    animation-delay: initial !important;
}

.cursor-pointer {
    cursor: pointer;
}

.react-tooltip {
    z-index: 100;

    @media (max-width: $mediumView) {
        display: none;
    }
}

.fancy-text {
    position: relative;
    white-space: nowrap;
    color: $theme;

    &:after {
        content: "";
        position: absolute;
        height: 0.15em;
        background-image: url($signatureSvg);
        background-size: auto 100%;
        background-repeat: no-repeat;
        background-position: 0em;
    }

    &.p::after {
        left: -10px;
        right: 0;
        top: 18px;
    }

    &.b::after,
    &.l::after,
    &.s::after,
    &.t::after,
    &.i::after,
    &.j::after {
        left: -5px;
        right: 0;
        top: 22px;
    }
}

.text-ellipse {
    text-wrap: nowrap;
    text-overflow: ellipsis;
}

.no-margin {
    margin: 0;
}

.tiny {
    font-size: 0.8em;
}

.left {
    float: left;
}

.right {
    float: right;
}

.blue {
    color: $blue !important;
}

.yellow {
    color: $yellow !important;
}

.red {
    color: $red !important;
}

.purple {
    color: $purple !important;
}

.orange {
    color: $orange !important;
}

.green {
    color: $green !important;
}

.themed {
    color: $theme !important;
}

.center {
    text-align: center;
}

.fade-in {
    opacity: 0;
    animation: fadeIn $transitionTimer ease-in-out forwards;
}

.scale-up {
    transform: scale(0);
    animation: scaleUp $transitionTimer ease-in-out forwards;
}

.fade-in-scale-up {
    opacity: 0;
    font-size: 0;
    animation: fadeInScaleUp $transitionTimer ease-in-out forwards;
}

.with-delay {
    transition-delay: $transitionTimer;
    animation-delay: $transitionTimer;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes scaleUp {
    0% {
        transform: scale(0);
    }

    100% {
        transform: scale(1);
    }
}

@keyframes fadeInScaleUp {
    0% {
        opacity: 0;
        font-size: 0;
    }

    50% {
        opacity: 0;
        font-size: 1em;
    }

    100% {
        opacity: 1;
        font-size: 1em;
    }
}

@keyframes float {
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-10px);
    }

    100% {
        transform: translateY(0);
    }
}

@keyframes borderDown {
    0% {
        background-position: 100% 100%;
    }

    100% {
        background-position: 0% 0%;
    }
}