@mixin slice($index) {
    $angle: 120deg * $index;
    transform: rotate($angle);
}

.center-chart {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .circle .border {
        z-index: 1;
        overflow: hidden;

        .content {
            overflow: hidden;
            position: relative;
            padding: 5px;

            .inner-circle {
                opacity: 0;
                width: 100%;
                height: 100%;
                padding: 5px;
                overflow: hidden;
                position: relative;
                border-radius: 100%;
                animation: fadeIn $transitionTimer linear forwards;
                animation-delay: 1s;

                .slice {
                    z-index: 1;
                    width: 200px;
                    height: 200px;
                    position: absolute;
                    text-align: center;

                    .background {
                        top: 0;
                        right: 0;
                        width: 100%;
                        height: 100%;
                        opacity: 0.8;
                        cursor: pointer;
                        position: absolute;
                        transform-origin: 0% 100%;
                        transition: $transitionTimer;

                        &:hover {
                            opacity: 1 !important;
                        }
                    }

                    &.inactive .background {
                        opacity: 0.4;
                    }

                    &.active .background {
                        opacity: 1;
                    }

                    span {
                        width: 80px;
                        position: absolute;
                    }

                    &:first-child {
                        left: 50%;
                        top: -44px;

                        .background {
                            transform: rotate(120deg) skewY(30deg);

                            span {
                                top: 120px;
                                left: 20px;
                                transform: skewY(-30deg) rotate(-120deg);
                            }
                        }
                    }

                    &:nth-child(2) {
                        top: -49px;
                        left: calc(50% - 3px);

                        .background {
                            transform: rotate(240deg) skewY(30deg);

                            span {
                                top: 100px;
                                left: 30px;
                                transform: skewY(-30deg) rotate(-240deg);
                            }
                        }
                    }

                    &:nth-child(3) {
                        top: -49px;
                        left: calc(50% + 3px);

                        .background {
                            transform: rotate(0deg) skewY(30deg);
                        }

                        span {
                            top: 109px;
                            left: 32px;
                            transform: skewY(-30deg);
                        }
                    }
                }
            }
        }
    }

    @media (max-width: $smallView) {
        .shape-container.circle {
            width: 200px !important;
            height: 200px !important;

            .slice {
                span {
                    font-size: 0.8em;
                }

                &:first-child {
                    top: -104px !important;

                    span {
                        top: 145px !important;
                        left: 0 !important;
                    }
                }

                &:nth-child(2) {
                    top: -109px !important;

                    span {
                        top: 140px !important;
                        left: 0 !important;
                    }
                }

                &:nth-child(3) {
                    top: -109px !important;

                    span {
                        top: 147px !important;
                        left: 0 !important;
                    }
                }
            }
        }
    }
}