.me-container {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .me {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;


        .shape-container.hexagon .border {
            z-index: 3;

            .content {
                padding: 10px;

                .picture {
                    height: 100%;
                    width: 100%;
                    opacity: 0;
                    background-size: cover;
                    background-position: center;
                    background-repeat: no-repeat;
                    background-image: url('../../static/img/me.png');
                    animation: fadeIn $transitionTimer linear forwards;
                    animation-delay: 1s;
                }
            }
        }

        .rotation {
            z-index: 3;
            position: absolute;
            opacity: 0;
            animation: fadeIn $transitionTimer ease-in-out forwards;
            animation-delay: 1.2s;
            border-radius: 1000px;

            .rotation-element {
                left: calc(50% - 19px);
                top: -40%;
                height: 90%;
            }

            .circle {
                z-index: 10;
            }
        }
    }

    @media (max-width: $smallView) {
        .shape-container.hexagon {
            width: 200px !important;
            height: 200px !important;
        }

        .rotation {
            width: 130px !important;
            height: 130px !important;

            .rotation-element {
                left: calc(50% - 19px);
            }
        }
    }
}