$lightBackgroundColor: #dfdfdf;
$lightBorderColor: white;
$lightBodyBackground: #c0c0c0f3;

.light {
    min-height: 100vh;
    background-repeat: repeat;
    background: linear-gradient($lightBodyBackground, $lightBodyBackground),
        url('../../static/img/background.jpg');
    background-size: 300px 300px;
    background-color: $backgroundColor;
    color: $black;

    .experiences {
        min-height: 100vh;
        background-repeat: repeat;
        background: linear-gradient($lightBodyBackground, $lightBodyBackground),
            url('../../static/img/background.jpg');
        background-size: 300px 300px;
        background-color: $backgroundColor;
    }

    .bar,
    .shape-container {
        .border {
            background-color: $lightBorderColor;

            &::before {
                border-bottom-color: $lightBorderColor !important;
            }

            &::after {
                border-top-color: $lightBorderColor !important;
            }
        }

        .content {
            background-color: $lightBackgroundColor !important;

            &::before {
                border-bottom-color: $lightBackgroundColor !important;
            }

            &::after {
                border-top-color: $lightBackgroundColor !important;
            }
        }
    }

    .shape-container {
        &.active {
            .border {
                background-color: $lightBorderColor;

                .content {
                    background-color: $lightBorderColor !important;
                }
            }
        }

        &.cursor-pointer:hover .content {
            background-color: darken($lightBorderColor, 5%) !important;
        }
    }

    nav .ornament {
        div {
            background-color: $lightBorderColor;
        }

        svg {
            stroke: $lightBorderColor;
        }
    }

    .center-line {
        background-color: $lightBorderColor;

        &::after,
        &::before {
            border-color: $lightBorderColor;
        }
    }

    .bar .border {
        p {
            color: $white
        }

        .liquid {
            svg {
                animation: fadeInLight 0.5s ease-in-out forwards;
                animation-delay: 1.2s;
            }

            &.bg-blue {
                background-color: $blue;
            }

            &.bg-red {
                background-color: $red;
            }

            &.bg-green {
                background-color: $green;
            }

            &.bg-purple {
                background-color: $purple;
            }

            &.bg-yellow {
                background-color: $yellow;
            }

            &.bg-orange {
                background-color: $orange;
            }
        }
    }

    .center-chart {
        .slice {
            color: $white;

            &:nth-child(1) .background {
                background: linear-gradient(darken($red, 0%), darken($red, 10%)) !important;
            }

            &:nth-child(2) .background {
                background: linear-gradient(darken($blue, 0%), darken($blue, 10%)) !important;
            }

            &:nth-child(3) .background {
                background: linear-gradient(darken($green, 0%), darken($green, 10%)) !important;
            }
        }
    }

    .name {
        color: $black;
    }

    .technologies .hexagon .content {
        color: $black;

        div {
            filter: brightness(0.5) invert(0) grayscale(1);
        }
    }

    .falling-border .border {
        background: linear-gradient($lightBorderColor 52%, $lightBackgroundColor 53%);
        background-size: 220% 220%;
        background-position: 100% 100%;
        animation: borderDown 0.6s linear forwards;
        animation-delay: 0.4s;
    }

    .rotation .rotation-circle,
    hr {
        border-color: $lightBorderColor;
    }
}

@keyframes fadeInLight {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 0.4;
    }
}