$hexagonClip: polygon(6.5% 25%, 50% 0, 93.5% 25%, 93.5% 75%, 50% 100%, 6.5% 75%);
$triangleClip: polygon(50% 0%, 0% 86%, 100% 86%);

@mixin octogon($color, $zIndex) {

    &:before {
        z-index: $zIndex;
        top: -5px;
        border-bottom: 20px solid $color;
        border-left: 20px solid #ffffff00;
        border-right: 20px solid #ffffff00;
    }

    &:after {
        z-index: $zIndex;
        bottom: -5px;
        border-top: 20px solid $color;
        border-left: 20px solid #ffffff00;
        border-right: 20px solid #ffffff00;
    }

}

.shape-container {
    width: 100%;
    height: 100%;
    position: relative;

    .border {
        width: 100%;
        height: 100%;
        position: relative;
        padding: $strokeWidth;
        background-color: $borderColor;

        .content {
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: $transitionTimer;
            background-color: $backgroundColor;
        }
    }

    &.octogon {
        width: auto;
        height: auto;

        .border {
            @include octogon($borderColor, 1);

            & .content {
                display: block;
                padding: 10px 20px;
                position: relative;
                background-color: $backgroundColor;

                p {
                    position: relative;
                    z-index: 10;
                }

                @include octogon($backgroundColor, 2);
            }
        }


        & .border:before,
        & .border:after,
        & .content:before,
        & .content:after {
            content: "";
            left: 0;
            width: 100%;
            position: absolute;
        }

        .triangle {

            & .border:before,
            & .border:after,
            & .content:before,
            & .content:after {
                content: none;
            }

            & .content {
                padding: 10px 0 0 0;
            }
        }

        .hexagon,
        .circle {

            & .border:before,
            & .border:after,
            & .content:before,
            & .content:after {
                content: none;
            }

            & .content {
                padding: 0;
                height: 100%;
                display: flex;
            }
        }
    }

    &.hexagon {

        .border,
        .content {
            clip-path: $hexagonClip;
        }
    }

    &.triangle {

        .border,
        .content {
            clip-path: $triangleClip;
        }

        .content {
            padding-top: 10px;
        }
    }

    &.circle {

        .border,
        .content {
            border-radius: 1000px;
        }
    }

    &.cursor-pointer {
        cursor: pointer;
    }

    &.square.unlocked .border {
        background: linear-gradient($purple, darken($purple, 20%)) !important;
    }

    &.circle.unlocked .border {
        background: linear-gradient($yellow, darken($yellow, 20%)) !important;
    }

    &.hexagon.unlocked .border {
        background: linear-gradient($red, darken($red, 20%)) !important;
    }
}

.shape.hexagon {
    clip-path: $hexagonClip;
}

.shape.triangle {
    clip-path: $triangleClip;
}